*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: sans-serif;
}
.cart-container{
    padding-top: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.cart-container h3{
    text-align: center;
    padding: 10px;
}
.cart{
    text-align: center;
    height: 40px;
    line-height: 50px;
    background-color:#eee;
    margin-right: 20px;
    color:black;
    box-shadow: 0px 2px 1px -1px rgb(0 0 0 / 20%), 0px 1px 1px 0px rgb(0 0 0 / 14%), 0px 1px 3px 0px rgb(0 0 0 / 12%);
}
.cart-container .card{
    background-color: aliceblue;
    width:50%;
    padding: 10px;
    margin-bottom: 10px;
    box-shadow: 0px 2px 1px -1px rgb(0 0 0 / 20%), 0px 1px 1px 0px rgb(0 0 0 / 14%), 0px 1px 3px 0px rgb(0 0 0 / 12%);
}
.cart-container .img-title{
    display: flex;
}
.cart-container .img{
    width: 150px;
    height: 100px;
    box-shadow: 0px 2px 1px -1px #eee, 0px 1px 1px 0px #eee, 0px 1px 3px 0px #eee;
}
.cart-container img{
    width: 100%;
    box-shadow: 0px 2px 1px -1px #eee, 0px 1px 1px 0px #eee, 0px 1px 3px 0px #eee;
    height: 100%;
}
.cart-container .price-incre-decre{
    display: flex;
    line-height: 10px;
    margin-top: 10px;
}
.cart-container .price-incre-decre button{
    width:45px;
}
.title-price-btn{
    padding: 5px;
}
.price-incre-decre .qty{
    text-align: center;
    line-height: 1;
    font-size: 30px;
    background-color: yellow;
    font-weight: 200;
}
.title-price-btn{
    display: flex;
    flex-direction: column;
}
.totalPrice{
    background-color: #eee;
    width:50%;
    padding: 10px;
    margin-bottom: 10px;
    box-shadow: 0px 2px 1px -1px rgb(0 0 0 / 20%), 0px 1px 1px 0px rgb(0 0 0 / 14%), 0px 1px 3px 0px rgb(0 0 0 / 12%);
}
.totalPrice span{
    
}
@media only screen and (max-width:500px){
    .cart-container .card{
        width:95%;
    }
    .totalPrice{
        width:95%;
    }
}